import { SliceZone, usePrismicDocumentByUID } from '@prismicio/react';
import Footer from './Footer';
import Thx from './Thx';

const Thanks = () => {
  const [page, { state }] = usePrismicDocumentByUID('homepage', 'main');
  return state === 'loaded' ? (
    <div>
      <SliceZone
        slices={page.data.body}
        components={{
          thanks: Thx,
        }}
      />
      <Footer />
    </div>
  ) : (
    'Loading...'
  );
};

export default Thanks;
